@import './media.scss';
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&family=Rokkitt:wght@700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Bitter:wght@500&display=swap');

body {
  // font-family: 'Roboto', sans-serif;
  background-color: #f2f2f2;
  font-family: 'Bitter', serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {

  font-family: 'Rokkitt', serif;
  margin-top: 0;
}

p {
  margin-top: 0;
  margin-bottom: 12px;

}

* {
  box-sizing: border-box;
}

.container {

  max-width: 1100px;
  margin: 0 auto;
  padding: 0 15px;
}

.header {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  margin-bottom: 70px;

  .layer {
    background-color: rgba(0, 0, 0, 0.57);
  }

  .logo-wrapper {
    display: flex;
    justify-content: center;
    padding-top: 140px;

    .logo {
      border: 5px solid #fff;
      padding: 15px;
      border-radius: 100%;
      cursor: pointer;

      .brand {
        font-size: 40px;
        color: #fff;
      }
    }

  }

  .header-text {
    padding-top: 20px;
    padding-bottom: 140px;
    text-align: center;

    h1 {
      color: #fff;
      font-size: 35px;
      margin-bottom: 15px;

      @include medium {
        font-size: 45px;
      }
    }

    p {
      font-size: 18px;
      color: #fff;

      @include medium {
        font-size: 24px;
      }

    }
  }

}

.recipeHeading {

  color: #03b962;
  text-align: center;
  font-size: 45px;
  margin-bottom: 70px;
}

.tabs {
  display: flex;
  justify-content: center;
  margin-bottom: 30px;


  .tablist {
    border-radius: 10px;
    width: 140px;
    display: flex;
    height: 80px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 5px;
    cursor: pointer;
    position: relative;

    @include medium {
      height: 100px;
    }

    @include default {
      height: 120px;
    }

    svg {
      font-size: 35px;

      @include medium {
        font-size: 50px;
      }

    }

    span {
      font-size: 14px;

      @include medium {
        font-size: 18px;
      }
    }

    &.active {
      background-color: #03b962;

      svg {
        color: #fff;
      }

      span {
        color: #fff;
      }

      &::after {
        position: absolute;
        content: '';
        border-top: 15px solid #03b962;
        border-left: 10px solid transparent;
        border-right: 10px solid transparent;
        bottom: -15px;

        @include medium {
          border-top: 20px solid #03b962;
          border-left: 15px solid transparent;
          border-right: 15px solid transparent;
          bottom: -20px;

        }


      }
    }
  }
}

.recipe_banner {
  background-color: #6b2929;
  padding: 20px;
  color: #fff;
  display: flex;
  margin-bottom: 40px;
  flex-direction: column-reverse;

  @include medium {
    padding: 60px 40px;
    flex-direction: row;
  }

  .left-col {
    @include medium {
      width: 50%;
    }

    .badge {
      letter-spacing: 1.5px;
      background-color: #03b962;;
      padding: 5px;
      border-radius: 3px;
      margin-bottom: 10px;
      display: inline-block;
      font-size: 16px;
    }

    h1 {
      font-size: 45px;
      margin-bottom: 10px;
    }

    p {
      font-size: 18px;
      margin-bottom: 10px;

      strong {
        margin-right: 5px;
      }
    }

    h3 {
      font-size: 30px;
      margin-bottom: 20px;
    }

    .ingredients {
      display: flex;
      gap: 100px;

      ul {
        padding: 0;
        display: flex;
        flex-flow: wrap;
        gap: 20px;
        margin-top: 10px;
        list-style: none;

        li {
          display: flex;
          font-size: 18px;
          margin-bottom: 10px;

          @include medium {
            margin-bottom: 10px;
            width: calc(50% - 20px);

          }

          svg {
            font-size: 18px;

          }

          span {
            width: calc(100% - 18px);
          }
        }
      }
    }

  }

  .right-col {

    display: flex;
    justify-content: center;
    align-items: center;

    @include medium {
      width: 50%;
      padding-top: 50px;
    }


    .image-wrapper {
      width: 100%;
      height: 280px;
      margin-bottom: 30px;

      @include medium {
        width: 350px;
        height: 350px;
      }

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;

        @include medium {
          border-radius: 100%;

        }
      }
    }
  }
}

.heading-line {
  font-family: 'Bitter', serif;
  padding: 10px 0;
  font-size: 35px;
  text-align: center;
  margin-bottom: 50px;
  color: #03b962;

  @include medium {

    font-size: 45px;
  }

  .input-wrapper {
    display: flex;
    width: 100%;
    padding: 20px;
    justify-content: center;
    margin: 0 auto;

    input {
      width: 80%;
      height: 45px;
      outline: none;
      border-radius: 8px 0 0 8px;
      border: 1px solid #03b962;
      font-size: 20px;
      padding: 5px 15px;
    }

    button {
      background-color: #03b962;
      outline: none;
      color: #fff;
      display: flex;
      align-items: center;
      justify-content: center;
      height: 45px;
      border: none;
      font-size: 18px;
      width: 50px;
      border-radius: 0 8px 8px 0;
      cursor: pointer;
    }
  }
}

.flexbox {
  display: flex;
  flex-flow: wrap;
  justify-content: center;
  gap: 20px;
  margin-bottom: 70px;

  .flexItem {
    background-color: #fff;
    border-radius: 6px;
    overflow: hidden;
    width: 100%;
    margin-bottom: 20px;

    @include small {
      width: calc(100%/2 - 20px);
    }

    @include default {
      width: calc(100%/4 - 20px);
    }


    .img-wrapper {
      margin-bottom: 10px;

      img {
        width: 100%;
        border-radius: 10px;
      }

      p {
        color: #29336b;
        padding: 0 15px;
      }

    }
  }


}

.loader {
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  position: fixed;
  align-items: center;
  justify-content: center;
  top: 0;
  left: 0;

  .spinner {

    border: 8px solid #f3f3f3;
    border-top: 8px solid #03b962;
    width: 60px;
    height: 60px;
    border-radius: 100%;
    animation: spin 2s linear infinite;
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }

}